<template>
	<view>
		<view class="banner">	
			<swiper class="swiper" indicator-dots=true>
				<swiper-item v-for="item in bannerList" :key="item.id">
					<view class="swiper-item uni-bg-red">
						<image :src="item.pic" mode="widthFix" ></image>
					</view>
				</swiper-item>
				
			</swiper>
		</view>
		
		<!-- icon图 -->
		
		<view class="icon">
			<view v-for="item in iconList" :key="item.id">
				<image :src="item.icon" mode=""></image>
				<view>
					{{item.name}}
				</view>
			</view>
		</view>
		
		
		<!-- 限时活动 -->
		<view class="activity">
			<view class="left">
				
				<image src="data:image/png;base64," mode=""></image>
				<view >
					限时活动:
				</view>
			</view>
			<view class="right">
				 <swiper class="swiper" vertical=true autoplay=true>
				    <swiper-item  v-for="item in activityList" :key="item.id">
				       <view class="text">
				       	{{item.name}}
				       </view>
					   <view class="price">
					   	<view class="old">
					   		{{item.price}}
					   	</view>
						<view class="new">
							{{item.promotionPrice}}
						</view>
					   </view>
				    </swiper-item>
				</swiper>
			</view>
		</view>
		
		<view class="main">
		<!-- 热门推荐 -->
			<view class="hot">
				<view class="title">
					热门推荐
				</view>
				<view class="banner">
					<swiper class="swiper"  display-multiple-items=3>
						<swiper-item  v-for="item in hotList" :key="item.id">
							<view class="swiper-item uni-bg-red">
								<image :src="item.pic"></image>
								<view class="">
									{{item.name}}
								</view>
							</view>
						</swiper-item>
					</swiper>
				</view>
			</view>
		<!-- 新品推荐 -->
			<view class="new">
				<view class="title">
					新品推荐
				</view>
				<view class="newList">
					<navigator url="">
						
					</navigator>
					<view class="newIte" v-for="item in newList" :key="item.id">
						<view class="aaa">
						<image class="img" :src="item.pic" mode=""></image>
						<view class="text">
							<view class="name">
								{{item.name}}
							</view>
							<view class="price">
								价格{{item.price}}
							</view>
						</view>
						</view>
					</view>
				</view>
			</view>
			
		
		<!-- 猜你喜欢 -->
		
		<view class="like">
			<view class="title">
				猜你喜欢
			</view>
			<view class="like_list">
				<view class="item" v-for="item in likeList" :key="item.id" @tap="goGoodInfo(item.id)">
					<image :src="item.pic" mode=""></image>
					<view class="text">
						<view class="name">
							{{item.brandName}}
						</view>
						
						<view class="price">
							<view class="left">
								￥{{item.price}}
							</view>
							<view class="right">
								乐居
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>
		</view>
		
		
	</view>
	
	
</template>

<script>
	import {bannerAds,categorys,recommendList,hotList,lejuLatestProducts,saleMostProducts} from"../../api/home/index.js"
	export default{
		data(){
			return{
				bannerList:[],
				iconList:[],
				activityList:[],
				hotList:[],
				newList:[],
				likeList:[]
			}
		},
		onLoad(){
			
			// 首页轮播数据
			bannerAds().then(res=>{

				this.bannerList=res.data.items
			});
			//分类icon图标数据
			categorys("1308336521604599809").then(res=>{
				this.iconList=res.data.category.children
			})
			//限时活动
			recommendList().then(res=>{
				this.activityList=res.data.items
			})	
			//hot
			hotList().then(res=>{
				this.hotList=res.data.items
			})
			//new
			lejuLatestProducts().then(res=>{
				this.newList=res.data.productList
			})
			//like
			saleMostProducts().then(res=>{
				console.log(res)
				this.likeList=res.data.items
			})
		},
			
		methods:{
			goGoodInfo(res){
				
				getApp().globalData.goodsInfoId = res
				uni.navigateTo({
				    url: '/pages/home/goodsInfo/index?id='+res
				});
			}
		}
	}
</script>

<style  lang="scss" scoped>
@import "./home.scss"
</style>
